import React, { FC, PropsWithChildren, ReactNode } from "react"
import cl from "./ButtonLarge.module.css"

type PropsType = {
  onClickHandler: () => void
  className?: string
  fullWidth?: boolean
}

export const ButtonLarge: FC<PropsWithChildren<PropsType>> = ({
  onClickHandler,
  children,
  className,
  fullWidth,
}) => {
  return (
    <button
      className={`${cl.largeBtn} ${fullWidth ? cl.full : ""} ${className}`}
      type={"submit"}
      onClick={onClickHandler}
    >
      {children}
    </button>
  )
}
